.hero{
  position: relative;
  height: 95vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 2rem;
  &:before{
    content: "";
    position: absolute;
    inset: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: -1;
  }
  &__video {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: translate(-50%, -50%);
    z-index: -2;
  }
  &__content{
    max-width: 80%;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    h1{
      font-weight: 800;
      font-size: clamp(2.5rem, 5vw, 4.5rem);
      color: $white;
      text-shadow: 0 0 10px rgba(0, 0, 0, 0.45);
      text-align: center;
      line-height: 1.1;
      letter-spacing: 0.03em;
    }
    p{
      font-weight: 400;
      font-size: clamp(1.2rem, 1.6vw, 1.6rem);
      color: $white;
      text-align: center;
      line-height: 1.3;
      text-shadow:
              0 0 10px rgba(0, 0, 0, 0.45),
              0 0 20px rgba(0, 0, 0, 0.25);
    }
  }
  &__buttons{
    display: flex;
    gap: 1rem;
    margin: auto;
    #primary{
        background-color: $light-blue;
        color: $white;
          &:hover{
              background-color: darken($light-blue, 5%);
          }
    }
    #secondary{
      background: $white;
      color: $text;
        &:hover{
            background-color: darken($white, 10%);
        }
    }
  }
  &__button{
    padding: 1rem 2rem;
    font-size: 1.2rem;
    font-weight: 600;
    border: none;
    border-radius: 15px;
    cursor: pointer;
    text-decoration: none;
    transition: background-color 0.3s ease;
  }
  &__wave {
    position: absolute;
    left: 0;
    bottom: -1px;
    width: 100%;
    line-height: 0;
  }

  &__wave svg {
    display: block;
    width: 100%;
    height: auto;
  }
}
.trips{
  padding: gap(md, rem) gap(xxl, rem);
  background: $background1;
  &__header{
    text-align: center;
    max-width: 700px;
    margin: 0 auto 3rem;
  }
  &__title{
    font-size: clamp(2rem, 3vw, 3rem);
    font-weight: 700;
    margin-bottom: .5rem;
    color: $text;
  }
  &__subtitle {
    font-size: 1.2rem;
    color: $text-dim;
  }
  &__grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
  }
}
.card{
  background: $white;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 4px 25px rgba(0,0,0,0.08);
  display: flex;
  flex-direction: column;
  transition: transform .3s ease, box-shadow .3s ease;
  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 30px rgba(0,0,0,0.15);
  }
  &__image img {
    width: 100%;
    height: 220px;
    object-fit: cover;
  }
  &__content {
    padding: 1rem 1.5rem;
  }
  &__title {
    font-size: 1.4rem;
    margin-bottom: .5rem;
    font-weight: 600;
  }
  &__text {
    color: $text-dim;
    line-height: 1.5;
  }
  &__meta {
    padding: 0.5rem 1.5rem;
    display: flex;
    flex-direction: column;
    gap: .4rem;
    color: $text-dim;
    font-size: 0.95rem;
  }
  &__meta-item {
    display: flex;
    align-items: center;
    gap: .5rem;
  }
  &__meta-icon {
    font-size: 1.1rem;
    color: $light-blue;
  }
  &__footer {
    padding: 1rem 1.5rem 1.5rem;
  }
  &__btn {
    display: block;
    width: 100%;
    text-align: center;
    padding: 0.9rem 1rem;
    border-radius: 999px;
    background: $light-blue;
    color: $white;
    font-weight: 600;
    text-decoration: none;
    transition: background .2s, transform .2s, box-shadow .2s;
    &:hover{
      :hover {
        background: $light-blue;
        transform: translateY(-1px);
        box-shadow: 0 6px 18px rgba(0, 0, 0, 0.15);
      }
    }
  }
}

.benefits{
  padding: gap(md, rem) gap(xxl, rem);
  background: $background2;
  &__header {
    text-align: center;
    max-width: 700px;
    margin: 0 auto 3rem;
  }
  &__title {
    font-size: clamp(2rem, 3vw, 3rem);
    font-weight: 700;
  }
  &__subtitle {
    font-size: 1.2rem;
    color: $text-dim;
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2.5rem;
  }
}

.benefit{
  text-align: center;
  padding: 2.5rem 1.5rem;
  border-radius: 20px;
  background: $background2;
  transition: transform .3s ease, box-shadow .3s ease;
  &:hover {
    transform: translateY(-6px);
    box-shadow: 0 12px 40px rgba(0,0,0,.13);
    background: $white;
  }
  &__icon {
    width: 70px;
    height: 70px;
    border-radius: 20px;
    background: linear-gradient(135deg, $light-blue, #00a0e0);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto 1.5rem;
  }
  &__icon svg {
    width: 32px;
    height: 32px;
  }
  &__title {
    font-weight: 600;
    font-size: 1.25rem;
  }
  &__text {
    color: $text-dim;
    line-height: 1.5;
  }
}
.contact{
  padding: gap(md, rem) gap(xxl, rem);
  background: $background1;
  &__container{
    display: flex;
    flex-direction: column;
    gap: gap(xl, rem);
  }
  &__header {
    text-align: center;
  }
  &__subtitle {
    font-size: 1.2rem;
    color: $text-dim;
  }
  &__grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3rem;
    align-items: start;
  }
  &__form .contact-form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
  &__form .form-group {
    display: flex;
    flex-direction: column;
  }
  &__form .form-group label {
    font-weight: 600;
  }
  &__form .input,
  .contact__form .textarea {
    width: 100%;
    padding: 1rem 1.2rem;
    border-radius: 12px;
    border: 1px solid $white;
    font-size: 1rem;
    outline: none;
    background: $white;
    transition: border .2s, box-shadow .2s, background .2s;
  }
  &__form .input:focus,
  .contact__form .textarea:focus {
    border-color: $light-blue;
    box-shadow: 0 0 0 3px rgba(0, 200, 255, 0.25);
    background: $white;
  }
  &__form .textarea {
    min-height: 150px;
    resize: vertical;
  }
  &__form .error {
    margin-top: .3rem;
    font-size: .9rem;
    color: #d93025;
  }
  &__form .btn-submit {
    margin-top: .5rem;
    padding: 1rem;
    background: linear-gradient(135deg, $light-blue, #00a0e0);
    color: $white;
    font-weight: 600;
    font-size: 1.1rem;
    border-radius: 12px;
    border: none;
    cursor: pointer;
    transition: background .2s, transform .2s, box-shadow .2s;
  }
  &__form .btn-submit:hover {
    background: linear-gradient(135deg, $light-blue, #008fcc);
    transform: translateY(-2px);
    box-shadow: 0 8px 25px rgba(0, 0, 0, .15);
  }
  &__aside {
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
}
.contact-card {
  background: $white;
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 4px 25px rgba(0,0,0,0.08);
  &--info .contact-card__row {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 1.5rem;
  }
  &--info .contact-card__row:last-child {
    margin-bottom: 0;
  }
  &__icon {
    width: 50px;
    height: 50px;
    border-radius: 12px;
    background: linear-gradient(135deg, $light-blue, #00a0e0);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__icon svg {
    width: 26px;
    height: 26px;
  }
  &__label {
    font-weight: 600;
    margin-bottom: .1rem;
  }
  &__value {
    color: $text-dim;
    line-height: 1.4;
    text-decoration: none;
  }
  &__value:hover {
    color: $light-blue;
  }
  &--cta {
    background: linear-gradient(135deg, #d8f4ff, #bbecff, #9de6ff);
  }
  &--cta .contact-card__title {
    font-size: 1.4rem;
    font-weight: 700;
    margin-bottom: .8rem;
  }
  &--cta .contact-card__text {
    color: $text-dim;
    margin-bottom: 1.3rem;
  }
  &--cta .contact-card__btn {
    display: inline-block;
    padding: .7rem 1.4rem;
    background: $white;
    color: $text-dim;
    font-weight: 600;
    border-radius: 12px;
    text-decoration: none;
    border: 1px solid #ddd;
    transition: background .2s, transform .2s, box-shadow .2s;
  }
  &--cta .contact-card__btn:hover {
    background: $background1;
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(0,0,0,.1);
  }
}